import * as Yup from 'yup';
import { useEffect, useState, } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// swal
import swal from 'sweetalert';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Alert } from '@mui/material';
import axios from '../../../../utils/axios';
import { FormProvider, RHFSelect, RHFTextField } from '../../../../components/hook-form';
// format number 
import { fCurrency, } from '../../../../utils/formatNumber';
import useAuth from '../../../../hooks/useAuth';
import { PATH_DASHBOARD } from '../../../../routes/paths';

// ----------------------------------------------------------------------

export default function PurchaseExam() {

  const NewUserSchema = Yup.object().shape({
    exam_name: Yup.string().required()
  });
  const pinSchema = Yup.object().shape({
    pin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),
    exam_name: Yup.string().required()
  });
  const { enqueueSnackbar } = useSnackbar();
  const [exam, setExam] = useState();
  const [amount, SetAmount] = useState();
  const [price, Setprice] = useState();
  const AccessToken = window.localStorage.getItem('accessToken');
  const { setting } = useAuth();
  const pin = setting?.setting?.allow_pin === 1;
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(pin ? pinSchema : NewUserSchema),
  });
  const [discount, SetDiscount] = useState();
  const {
    watch,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;
  const values = watch();

  // get network over here
  useEffect(() => {
    axios.get('/api/website/app/cable/lock').then(data => {
      setExam(data.data?.cable);
    });
    axios.get('/api/resultprice/admin/secure').then(data => Setprice(data.data.price));
  }, []);
  const examlist = [
    { name: 'WAEC', code: 1 },
    { name: 'NECO', code: 2 },
    { name: 'NABTEB', code: 3 }
  ];

  const onSubmit = async (data) => {
    const value = examlist.filter((item) => item.code.toString() === data.exam_name)
    let examName = '';
    if (value[0]) {
      examName = value[0].name
    }
    try {
      const isConfirmed = await swal({
        title: 'Confirmation',
        text: `You are purchasing ${amount} result checker tokens for ${examName} at a total cost of ${discount}.`,
        icon: 'warning',
        buttons: ['Cancel', 'Confirm'],
      });
      if (isConfirmed) {
        const resp = await axios.post('/api/exam', {
          exam: data.exam_name,
          quantity: amount,
          pin: data.pin,
          token: AccessToken,
        })
        if (resp?.data?.status === 'success') {
          swal({
            title: 'success', text: `${resp?.data?.message}`, icon: 'success', buttons: [
              'Okay',
              'Invoice'
            ],
            dangerMode: true,
          }).then((isConfirm) => {
            if (isConfirm) {
              navigate(`${PATH_DASHBOARD.general.invioce}/${resp?.data?.transid}/result`, { replace: true });
            }
            window.location.reload();
          });
        } else if (resp?.data?.status === 'fail') {
          swal('Opps', `${resp?.data?.message}`, 'error');
        } else {
          swal('Info', `${resp?.data?.message}`, 'info');
        }
      }
    } catch (error) {
      if (error?.data?.message !== undefined) {
        swal('Opps', `${error?.data?.message}`, 'error');
      } else if (error?.message !== undefined) {
        swal('Opps', `${error?.message}`, 'error');
      } else {
        swal('Opps', 'Something went wrong', 'error');
      }
    }
  };


  const Calculate = async (data) => {
    SetAmount(data.target.value);
    const quantity = data.target.value;
    if (quantity) {
      if (quantity > 0) {
        if (quantity <= 5) {
          if (values.exam_name === '1') {
            SetDiscount(`₦${fCurrency(price?.waec * quantity)}`)
          } else if (values.exam_name === '2') {
            SetDiscount(`₦${fCurrency(price?.neco * quantity)}`)
          } else if (values.exam_name === '3') {
            SetDiscount(`₦${fCurrency(price?.nabteb * quantity)}`)
          } else {
            console.log(values)
            SetDiscount(undefined)
            enqueueSnackbar('select exam name', { variant: 'error' })
          }
        } else {
          SetDiscount('Maximum Quantity is 5')
        }
      } else {
        SetDiscount('Minimum Qunatity is 1');
      }
    } else {
      SetDiscount(undefined)
    }
  }


  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFSelect name="exam_name" label="Exam Name">
                <option value="" />
                {exam !== undefined && examlist?.map((option) => (
                  <option key={option?.code} value={option?.code} disabled={option?.code === 1 && exam?.waec === 0 || option?.code === 2 && exam?.neco === 0 || option?.code === 3 && exam?.nabteb === 0}>
                    {option?.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField name="quantity" label="Quantity" type="number" autoComplete="new-password" value={amount} inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} onChange={Calculate} />
              {pin && <RHFTextField name="pin" label="Transaction Pin" type="password" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />}

              {discount !== undefined && <RHFTextField name="charges" label="Charges" value={discount} disabled />}
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Purchase
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
